*, :before, :after {
  box-sizing: border-box;
}

:root {
  --page-background-color: #2c2c2c;
  font-size: 62.5%;
}

html {
  color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

body {
  background-color: var(--page-background-color);
  font-size: 2.5rem;
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: #fff;
  border-radius: 10px;
}

#site::-webkit-scrollbar-track {
  background-color: #ffffff5d;
}

#site {
  --max-header-bar-height: 65px;
  width: 100vw;
  height: calc(100vh - var(--max-header-bar-height) );
  margin-top: var(--max-header-bar-height);
  scrollbar-color: white transparent;
  scrollbar-width: thin;
  overflow-x: hidden;
}

.Button__To-Top {
  width: 40px;
  height: 40px;
  z-index: 777;
  opacity: .3;
  background-color: #2e2e2e;
  border-radius: 100%;
  margin-bottom: 20px;
  margin-right: 20px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  right: 0;
  box-shadow: 0 0 3px 2px #fff;
}

.Button__To-Top:hover {
  opacity: 1;
}

.Button__To-Top__Arrow {
  --arrow-style: 4px solid #949494;
  border-bottom: var(--arrow-style);
  border-left: var(--arrow-style);
  width: 40%;
  height: 40%;
  display: inline-block;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -75%)rotateZ(135deg);
}

@media screen and (min-width: 400px) {
  .word-wrap-specific {
    display: none;
  }
}

@media screen and (min-width: 1250px) {
  #site {
    --max-header-bar-height: 85px;
  }

  body {
    font-size: 2rem;
  }

  .Button__To-Top__Arrow {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)rotateZ(135deg);
  }
}

