:root {
  --color-background-dark: #2a2a2a;
  --color-main-aero: paleturquoise;
  --header-color: #fff;
  --section-shadow-size: 5px;
  --section-shadow-bottom: 0 var(--section-shadow-size) 2px 0px var(--header-color);
  --section-shadow-top: 0 calc(-1 * var(--section-shadow-size) ) 1px 0px var(--header-color);
}

.Section__Heading {
  text-align: center;
  word-wrap: break-word;
  padding: 100px 1rem 250px;
}

@media screen and (min-width: 640px) {
